.headerStyle {
  color: white;
}

.headerStyle img {
  width: 100px;
  height: 30px;
  margin: -25px 40px;
  margin-top: -40px;
  background-size: cover;
}

.headerStyle .name {
  display: inline-block;
  font-size: 25px;
  margin: 10px 40px;
}

.headerStyle .title {
  font-size: 20px;
  list-style: none;
  display: inline-block;
}

.headerStyle .title li {
  display: inline-block;
  margin-left: 80px;
}

.headerStyle .title li a {
  color: white;
}

.headerStyle .title li a:hover {
  color: black;
}

.headerStyle .button {
  float: right;
  margin-right: 20px;
  margin-top: 20px;
  color: black;
  background: white;
  border: 1px solid white;
  border-radius: 5px;
}

.headerStyle .button:hover {
  color: white;
  background: #20B2AA;
}