.tagWrapper,
.addThemeWrapper {
  margin-top: 30px;
}

.tagWrapper .button,
.addThemeWrapper .button {
  margin-left: 30vw;
  background: #00ADAF;
  border: 1px solid white;
  border-radius: 5px;
}

.tagWrapper .button:hover,
.addThemeWrapper .button:hover {
  background: #00868B;
}

.editWrapper {
  margin-left: calc(50% - 500px);
}

.editWrapper .vedioArea {
  width: 1000px;
  padding: 20px;
  background: white;
}

.editWrapper .img {
  width: 960px;
}

.editWrapper .title {
  font-size: 24px;
  line-height: 1;
  font-weight: 500;
  color: #212529;
  margin-top: 20px;
  margin-bottom: 20px;
}