.adWrapper {
  position: fixed;
  background: white;
  top: 130px;
  right: 20px;
  width: 200px;
  height: 160px;
  padding: 10px;
  z-index: 1;
}

.adWrapper img {
  width: 180px;
  height: 140px;
}

.firstPageWrapper .input {
  width: 800px;
  height: 40px;
  border-radius: 5px;
  margin-top: 20px;
  margin-left: 100px;
}

.firstPageWrapper .button {
  width: 60px;
  height: 40px;
  margin-left: -60px;
  border-radius: 5px;
  background: #388E8E;
  color: white;
  font-weight: 500;
}

.page {
  margin-left: 280px;
}

.background {
  width: 100%;
  height: 120px;
}

.theme-card {
  font-size: 14px;
  font-weight: 500;
  color: #333;
}

.theme-card .card:hover {
  background: #fff;
}

.theme-card .card {
  margin: 8px 0;
  padding: 10px;
  height: 200px;
}

.theme-card .card-title {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.theme-card:hover .card-title {
  color: #388E8E;
}

.theme-card .text {
  font-size: 12px;
  opacity: 0.6;
}

.theme-card:hover .text {
  color: #333;
  opacity: 0.6;
}

.theme-card .pictureText {
  position: absolute;
  top: 60px;
  left: 40px;
  width: 180px;
  text-align: center;
  color: #fff;
  font-size: 16px;
  white-space: nowrap;
  overflow: hidden;
  font-weight: 800;
}

.type {
  margin-top: 20px;
}

.type1 {
  margin-top: 10px;
}